@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@600;700&display=swap);
@media screen and (max-width: 1400px){.container{max-width:1200px;margin:0 auto}.nav{width:100%;padding-left:16px;padding-right:16px;background-color:#fff;height:90px}.nav ul{display:flex;flex-direction:row;justify-content:space-between;height:100%;align-items:center}.nav ul li{list-style:none}.nav ul a .navbar_icon{transition:all 0.3s ease;fill:#c2c6ce}.nav ul a .navbar_icon:hover{fill:#c77f7f}.nav ul a .navbar_icon:focus-visible,.nav ul a .navbar_icon:active{fill:#fc1055}}@media screen and (max-width: 1200px){.container{max-width:1000px}}@media screen and (max-width: 992px){.container{max-width:700px}}@media screen and (max-width: 768px){.container{max-width:560px}}@media screen and (max-width: 576px){.container{max-width:300px}}

.detailed-card-component{max-width:80%;height:256px;border-radius:20px;background-size:cover;position:relative;color:#ffffff;font-weight:600;padding:16px 16px 16px 16px;font-size:12px;background-repeat:no-repeat;margin-bottom:0 auto}.detailed-card-component .close-btn{width:30px;height:30px;border-radius:50px;background-color:#989898;border:none;position:absolute;right:5px;top:5px;cursor:pointer;display:flex;align-items:center;justify-content:center}.detailed-card-component .close-btn:hover .close-icon{fill:#c77f7f}.detailed-card-component .close-btn .close-icon{transition:all 0.3s ease;fill:#ffffff}.detailed-card-component .data{margin-top:160px;margin-bottom:5px;letter-spacing:0.5px;line-height:16px}.detailed-card-component .name{font-size:22px;line-height:24px;margin-bottom:10px}.detailed-card-component .bottom-block{display:flex;justify-content:start;color:#bdc0c4;align-items:center}.detailed-card-component .bottom-block .note-icon{fill:#bdc0c4;width:10px;height:10px;margin-right:7px}.detailed-card-component .bottom-block .genre{margin-right:7px;letter-spacing:1px}.detailed-card-component .bottom-block .tickets-icon{fill:#bdc0c4;width:11px;height:12px;margin-right:7px}.detailed-card-component .bottom-block .price .uah-icon{fill:#bdc0c4;width:8px;height:8px;margin-right:2px}

.short-info-card{max-width:80%;height:160px;border-radius:20px;background-position:center;background-size:contain;padding:13px 16px;position:relative}.short-info-card .name{color:#fff;font-size:22px;margin-bottom:8px}.short-info-card .genre{color:#bdc0c4;font-size:12px}.short-info-card .genre .note-icon{fill:#bdc0c4}.short-info-card .genre-label{margin-left:5px}.short-info-card .link{color:#fff;font-size:15px;text-decoration:none;position:absolute;bottom:13px}

@media screen and (max-width: 1400px){.container{max-width:1200px;margin:0 auto;padding:16px}.container .title-block{display:flex;justify-content:space-between;margin-bottom:30px;font-weight:700;font-size:28px;align-items:center}.container .title-block .settings-btn{border:none;border-radius:50%;width:32px;height:32px;background:#ffffff;cursor:pointer;display:flex;justify-content:center;align-items:center}.container .title-block .settings-btn .settings-icon{fill:#fc1055}}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:'';content:none}q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

*{font-family:'Work Sans', sans-serif}body{background:#f2f2f2}

